export default {
  props: {
    label: String,
    value: Boolean,
    activeText: {
      type: String,
      default: "Active",
    },
    inactiveText: {
      type: String,
      default: "Inactive",
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    width: {
      type: String,
      default: "120px",
    },
    labelCSS: {
      type: String, 
      default: ""
    }
  },
  data() {
    return {
      toggleOn: false,
    };
  },
  methods: {
    toggleState() {
      this.toggleOn = !this.value;
      this.$emit("input", this.toggleOn);
    },
  },
};
