export default {
  props: {
    model: String,
  },
  data() {
    return {
      editMode: false,
      doc: {
        _id: "",
        firstName: "",
        surName: "",
        countryDialCode: "",
        countryIsoCode: "",
        phoneNumber: "",
        usergroup: "",
        contentUnlock: {
          status: false,
          code: "",
        },
        createdAt: "",
        status: false,
        cart: []
      },
      loading: false,
      updated: false,
    };
  },
  methods: {
    async updateDocument(model, details, editMode) {
      this.loading = true;
      const result = await this.$updateDocument(
        this.model,
        this.doc,
        this.editMode
      );
      this.loading = false;

      if (!result.updated) return;

      this.$emit("updated");
      this.populateForm(result.doc);
      this.$flash(this);
    },
    async deleteDocument() {
      this.loading = true;
      const result = await this.$deleteDocument(this.model, this.doc._id);
      this.loading = false;

      if (!result.deleted) return;

      this.$emit("updated");
      this.resetForm();
      this.$flash(this);
    },
    populateForm(details) {
      console.log(details,'-Users-')
      const {
        _id,
        firstName,
        surName,
        countryDialCode,
        countryIsoCode,
        phoneNumber,
        usergroup,
        contentUnlock,
        createdAt,
        cart,
        status,
      } = details;
      this.doc = {
        _id,
        firstName,
        surName,
        countryDialCode,
        countryIsoCode,
        phoneNumber,
        usergroup,
        contentUnlock:
          contentUnlock === undefined
            ? { status: false, code: "" }
            : contentUnlock,
        createdAt,
        cart: cart ? cart : [],
        status,
      };
      this.editMode = true;
    },
    closeForm() {
      this.resetForm();
      this.$emit("close");
    },
    resetForm() {
      this.populateForm({
        _id: "",
        firstName: "",
        surName: "",
        countryDialCode: "",
        countryIsoCode: "",
        phoneNumber: "",
        usergroup: "",
        contentUnlock: {
          status: false,
          code: "",
        },
        createdAt: "",
        status: false,
      });

      this.editMode = false;
    },
  },
};
