export default {
  layout: "admin",
  data() {
    return {
      showForm: false,
      loading: false,
      model: "users",
      /* rawCriterion */
      rawCriterion: {
        search: {
          key: "firstName",
          term: "",
        },
        filters: {
          type: "default",
        },
        sortBy: {},
        limit: 20,
      },
      list: [],
      sortByFields: [
        "firstName",
        "surName",
        "usergroup",
        "countryIsoCode",
        "viewCount",
        "lastSeen",
        "status",
      ],
      headings: [
        "_id",
        "firstName",
        "surName",
        "usergroup",
        "countryIsoCode",
        "viewCount",
        "lastSeen",
        // "cartCount",
        "status",
      ],
      dragEnabled: false,
    };
  },
  async mounted() {
    // await this.fetchList();
  },
  methods: {
    clearFilters(dragEnabled) {
      this.dragEnabled = dragEnabled;

      this.rawCriterion.filters = {
        type: "default",
      };

      this.rawCriterion.search.term = "";
    },
    updateList() {
      this.$refs.pagination.fetchResults();
    },
    sortToggled(sortBy) {
      console.log(sortBy);
      this.rawCriterion = {
        ...this.rawCriterion,
        sortBy,
      };
    },
    documentFetched(doc) {
      this.showForm = true;
      this.editMode = true;
      this.$refs.updateComponent.populateForm(doc);
    },
    async resultsFetched(result) {
      if (result.docs.length === 0) {
        this.list = [];
        return;
      }

      /* extract list */
      this.list = result.docs.map(
        ({
          _id,
          firstName,
          surName,
          usergroup,
          countryIsoCode,
          viewCount,
          lastSeen,
          status,
          cart,
        }) => {
          return {
            _id,
            firstName,
            surName,
            usergroup,
            countryIsoCode,
            viewCount: viewCount || 0,
            lastSeen: lastSeen ? this.$formatDate(lastSeen) : "N/A",
            // cartCount: cart ? cart.length : 0,
            status,
          };
        }
      );
    },
  },
};
